import $ from 'jquery';

export default $(function() {
  $('#sign-page .btn-sign').on('click', sign);
});

const sign = () => {
  const rootEl = $('#sign-page');
  const signTrxEl = $('.transaction', rootEl);
  const privateKeyEl = $('.private-key', rootEl);
  const signedTrxEl = $('.signed-transaction', rootEl);


  const trx = JSON.parse(signTrxEl[0].value);

  const signedTrx = window.scorum.auth.signTransaction(trx, [privateKeyEl[0].value]);

  signedTrxEl.val(JSON.stringify(signedTrx));
}

