import ecurve from 'ecurve';
import BigInteger from 'bigi';
import base58 from 'bs58';
import { sha256, sha512 } from './hash';
import PublicKey from './public-key';

// var assert = require('assert');

const secp256k1 = ecurve.getCurveByName('secp256k1');

export default class PrivateKey {
  constructor(d) {
    this.d = d;
    this.public_key = '';
  }

  static fromBuffer(buf) {
    if (!Buffer.isBuffer(buf)) {
      throw new Error('Expecting paramter to be a Buffer type');
    }

    if (buf.length !== 32) {
      console.log(`WARN: Expecting 32 bytes, instead got ${buf.length}, stack trace:`, new Error().stack);
    }

    if (buf.length === 0) {
      throw new Error('Empty buffer');
    }

    return new PrivateKey(BigInteger.fromBuffer(buf));
  }

  static fromSeed(seed) { // generate_private_key
    if (!(typeof seed === 'string')) {
      throw new Error('seed must be of type string');
    }

    return PrivateKey.fromBuffer(sha256(sha512(`${seed} 0`)));
  }

  toBuffer() {
    return this.d.toBuffer(32);
  }

  static fromWif(_privateWif) {
    const privateWif = new Buffer(base58.decode(_privateWif));

    // const version = privateWif.readUInt8(0);
    // assert.equal(0x80, version, `Expected version ${0x80}, instead got ${version}`);
    // checksum includes the version

    let privateKey = privateWif.slice(0, -4);
    const checksum = privateWif.slice(-4);
    let newChecksum = sha256(privateKey);
    newChecksum = sha256(newChecksum);
    newChecksum = newChecksum.slice(0, 4);
    if (checksum.toString() !== newChecksum.toString()) {
      throw new Error('Invalid WIF key (checksum miss-match)');
    }

    privateKey = privateKey.slice(1);
    return PrivateKey.fromBuffer(privateKey);
  }

  static checkSeedWithPK(memo, pk) {
    return this.fromSeed(memo).toPublicKey().toPublicKeyString() === pk;
  }

  toWif() {
    let privateKey = this.toBuffer();
    // checksum includes the version
    privateKey = Buffer.concat([new Buffer([0x80]), privateKey]);

    let checksum = sha256(privateKey);
    checksum = sha256(checksum);
    checksum = checksum.slice(0, 4);

    const privateWif = Buffer.concat([privateKey, checksum]);

    return base58.encode(privateWif);
  }

  toPublicKeyPoint() {
    return secp256k1.G.multiply(this.d);
  }

  toPublic() {
    if (this.public_key) {
      return this.public_key;
    }

    this.public_key = PublicKey.fromPoint(this.toPublicKeyPoint());

    return this.public_key;
  }

  toPublicKey() {
    return this.toPublic();
  }

  toString() {
    return this.toWif();
  }
}
