import $ from 'jquery';
import Inputmask from "inputmask/dist/inputmask/inputmask.numeric.extensions";

export default $(function() {
  // data-inputmask="'alias': 'decimal', 'groupSeparator': ',', 'autoGroup': true, 'digits': 2, 'digitsOptional': false, 'prefix': ' ', 'placeholder': '0', 'showMaskOnHover': false, 'rightAlign': false, 'showMaskOnFocus': false"
  Inputmask({
    alias: 'decimal',
    groupSeparator: '',
    autoGroup: true,
    digits: 9,
    digitsOptional: false,
    prefix: ' ',
    placeholder: '0.000000000',
    showMaskOnHover: false,
    rightAlign: false,
    showMaskOnFocus: false
  }).mask($('#scorum-power'));

  $('#delegate-power-page .from-name').on('focusout', (e) => getFromAccount(e.target.value));
  $('#delegate-power-page .to-name').on('focusout', (e) => getToAccount(e.target.value));
  $('#delegate-power-page .delegate-btn').on('click', () => getTransaction((trx) => {
    $('#delegate-power-page .output').val(JSON.stringify(trx));
  }));
  $('#delegate-power-page #sign-and-broadcast .btn-broadcast').on('click', signAndBroadcast);
});

const getTransaction = (callback = () => {}) => {
  const rootEl = $('#delegate-power-page');
  const fromNameEl = $('.from-name', rootEl);
  const toNameEl = $('.to-name', rootEl);
  const amountSpEl = $('.amount-sp', rootEl);

  const fromName = fromNameEl[0].value.trim();
  const toName = toNameEl[0].value.trim();
  const amountSp = amountSpEl[0].value.trim();

  const trx = {
      extensions:[],
      operations:[["delegate_scorumpower", { delegator: fromName, delegatee: toName, scorumpower: `${amountSp} SP` }]]
  }

  return window.scorum.broadcast._prepareTransaction(trx).then(preparedTrx => {
      preparedTrx.expiration = preparedTrx.expiration.toISOString().replace(/\.\d+Z/,'');

      callback(preparedTrx);

      return preparedTrx;
  });
}

const signAndBroadcast = () => {
  const privateKey = $('#sign-and-broadcast .private-key')[0].value;

  getTransaction()
    .then((trx) => window.scorum.auth.signTransaction(trx, [privateKey]))
    .then(window.scorum.broadcast.send)
    .then(() => {
      $('#sign-and-broadcast .private-key')[0].value = '';
    })
};

const getFromAccount = (username) => {
  const rootEl = $('#delegate-power-page');

  window.scorum.api.getAccountsAsync([username])
  .then((res) => {
      if (res.length) {
        $('.from-values', rootEl).css('display', 'block');
        $('.from-values .my-sp .value', rootEl).text(res[0].scorumpower);
        $('.from-values .delegated-sp .value', rootEl).text(res[0].delegated_scorumpower);
      }
  })
}

const getToAccount = (username) => {
  const rootEl = $('#delegate-power-page');

  window.scorum.api.getAccountsAsync([username])
  .then((res) => {
      if (res.length) {
        $('.to-values', rootEl).css('display', 'block');
        $('.to-values .my-sp .value', rootEl).text(res[0].scorumpower);
        $('.to-values .received-sp .value', rootEl).text(res[0].received_scorumpower);
      }
  })
}

const clearKeys = () => {
  const rootEl = $('#delegate-power-page');
  $('.from-values', rootEl).css('display', 'block');
  $('.to-values', rootEl).css('display', 'block');
  $('.from-values .my-sp .value', rootEl).text('');
  $('.from-values .delegated-sp .value', rootEl).text('');
  $('.to-values .my-sp .value', rootEl).text('');
  $('.to-values .received-sp .value', rootEl).text('');
}
