import $ from 'jquery';
import prepareTransaction from './helpers';

export default $(function() {
  $('#proposals-page #proposalModal .proposal-create-btn').on('click', () => {
    const type = $('#proposals-page #proposalModal').data('type');
    console.log(type);
    switch (type) {
      case 'comm-add-member':
        operationWithAccountName('registration_committee_add_member');
        break;
      case 'comm-exclude-member':
        operationWithAccountName('registration_committee_exclude_member');
        break;
      case 'dev-add-member':
        operationWithAccountName('development_committee_add_member');
        break;
      case 'dev-exclude-member':
        operationWithAccountName('development_committee_exclude_member');
        break;
      case 'dev-withdraw-vesting':
        proposalDevWithdrawVesting();
        break;
      case 'dev-transfer':
        proposalDevTransfer();
        break;

      default: null;
    }

    $('#proposals-page #proposalModal').modal('hide');
  });

  if (window.location.pathname.indexOf('/proposals.html') !== -1) {
    window.scorum.api.lookupProposalsAsync().then((res) => {
      res.forEach((proposal, i) => {
        $('#proposals-page .proposals-list').append(`
          <div class="card">
            <div class="card-header" id="headingOne">
              <h5 class="mb-0">
                <div class="proposal-item" data-toggle="collapse" data-target="#proposal-${i}" aria-expanded="true" aria-controls="collapseOne">
                  Creator: <b>${proposal.creator}</b> Operation: <span>${proposal.operation[0]}</span>
                  <div class="expiration">expiration: ${proposal.expiration}</div>
                  <span class="badge badge-warning">ID: ${proposal.id}</span>
                </div>
              </h5>
            </div>

            <div id="proposal-${i}" class="collapse" aria-labelledby="headingOne" data-parent="#accordion">
              <div class="card-body">
                <p><b>ID</b>: ${proposal.id}</p>
                <p><b>Creator</b>: ${proposal.creator}</p>
                <p><b>Expiration</b>: ${proposal.expiration}</p>
                <p><b>Operation</b>: ${proposal.operation[0]}</p>
                <p><b>Operation arguments</b>: ${JSON.stringify(proposal.operation[1])}</p>
                <p><b>Quorum percent</b>: ${proposal.quorum_percent}</p>
                <p><b>Voted accounts</b>: ${JSON.stringify(proposal.voted_accounts)}</p>
                <hr />
                <button type="button" class="btn btn-primary proposal-vote" data-id="${proposal.id}">Vote for proposale</button>
                <span> by <input placeholder="username..." class="proposa-vote-by"></span>
              </div>
            </div>
          </div>
        `);
        $('#proposals-page .proposal-vote').on('click', (e) => {
          e.stopImmediatePropagation();
          const input = $(e.target).closest('.card-body').find('.proposa-vote-by');
          proposalVote($(e.target).data('id'), $(input).val(), () => {
            input.val('');
          });
        });
      });
    })
  }

  $('#proposals-page #proposalModal').on('hide.bs.modal', (event) => {
    $('#proposals-page #proposalModal').data('type', '');
  });

  $('#proposals-page #proposalModal').on('show.bs.modal', (event) => {
    const button = $(event.relatedTarget);
    const type = button.data('type');

    var title = button.data('title');
    var modal = $(this);
    modal.find('.modal-title').text(`Create Proposal: ${title}`);

    modal.find('#proposalModal').data('type', type);

    const formEl = $('#proposals-page #proposalModal .modal-body form');
    $(formEl).find('.account-name').hide();
    $(formEl).find('.to-account').hide();
    $(formEl).find('.amount').hide();
    $(formEl).find('.vesting-shares').hide();

    switch (type) {
      case 'comm-add-member':
        $(formEl).find('.account-name').show();
        break;
      case 'comm-exclude-member':
        $(formEl).find('.account-name').show();
        break;
      case 'dev-add-member':
        $(formEl).find('.account-name').show();
        break;
      case 'dev-exclude-member':
        $(formEl).find('.account-name').show();
        break;
      case 'dev-withdraw-vesting':
        $(formEl).find('.vesting-shares').show();
        break;
      case 'dev-transfer':
        $(formEl).find('.to-account').show();
        $(formEl).find('.amount').show();
        break;

      default: null;
    }
  })
});

const proposalVote = (id, by, callback) => {
  const ouputEl = $('#proposals-page .output');

  const trx = {
    extensions:[],
    operations:[["proposal_vote", { voting_account: by, proposal_id: id }]]
  }

  prepareTransaction(trx, ouputEl);
}

const operationWithAccountName = (operationName) => {
  const roolEl = $('#proposals-page #proposalModal');
  const creatorNameEl = $('#creator-name', roolEl);
  const accountNameEl = $('#account-name', roolEl);
  const lifetimeEl = $('#lifetime', roolEl);

  const creatorName = creatorNameEl[0].value.trim();
  const accountName = accountNameEl[0].value.trim();
  const lifetime = lifetimeEl[0].value.trim();

  const ouputEl = $('#proposals-page .output');

  const trx = {
    extensions:[],
    operations:[["proposal_create", {
      creator: creatorName,
      lifetime_sec: Number(lifetime) * 86400,
      operation: [
        operationName,
        {
          account_name: accountName
        }
      ]
    }]]
  }

  prepareTransaction(trx, ouputEl);
}

const proposalDevWithdrawVesting = () => {
  const roolEl = $('#proposals-page #proposalModal');
  const creatorNameEl = $('#creator-name', roolEl);
  const amountEl = $('#vesting-shares', roolEl);
  const lifetimeEl = $('#lifetime', roolEl);

  const creatorName = creatorNameEl[0].value.trim();
  const amount = amountEl[0].value.trim();
  const lifetime = lifetimeEl[0].value.trim();

  const ouputEl = $('#proposals-page .output');

  const trx = {
    extensions:[],
    operations:[["proposal_create", {
      creator: creatorName,
      lifetime_sec: Number(lifetime) * 86400,
      operation: [
        "development_committee_withdraw_vesting",
        {
          vesting_shares: `${amount} SP`
        }
      ]
    }]]
  }

  prepareTransaction(trx, ouputEl);
}

const proposalDevTransfer = () => {
  const roolEl = $('#proposals-page #proposalModal');
  const creatorNameEl = $('#creator-name', roolEl);
  const toAccountEl = $('#to-account', roolEl);
  const amountEl = $('#amount', roolEl);
  const lifetimeEl = $('#lifetime', roolEl);

  const creatorName = creatorNameEl[0].value.trim();
  const toAccount = toAccountEl[0].value.trim();
  const amount = amountEl[0].value.trim();
  const lifetime = lifetimeEl[0].value.trim();

  const ouputEl = $('#proposals-page .output');

  const trx = {
    extensions:[],
    operations:[["proposal_create", {
      creator: creatorName,
      lifetime_sec: Number(lifetime) * 86400,
      operation: [
        "development_committee_transfer",
        {
          amount: `${amount} SCR`,
          to_account: toAccount
        }
      ]
    }]]
  }

  prepareTransaction(trx, ouputEl);
}
