import $ from 'jquery';

function SortByVotes(as, bs){
  var a, b, a1, b1, i= 0, n, L,
  rx=/(\.\d+)|(\d+(\.\d+)?)|([^\d.]+)|(\.\D+)|(\.$)/g;
  if ( as.votes === bs.votes) return 0;
  a = as.votes.toLowerCase().match(rx);
  b = bs.votes.toLowerCase().match(rx);
  L = a.length;
  while (i < L){
      if (!b[i]) return 1;
      a1 = a[i],
      b1 = b[i++];
      if(a1 !== b1){
          n = a1 - b1;
          if(!isNaN(n)) return n;
          return a1 > b1? 1:-1;
      }
  }
  return b[i]? -1:0;
}

const numberWithCommas = (x) => {
  return x.toString().replace(/\B(?=(\d{9})+(?!\d))/g, ",");
}

export default $(function() {
  $('#vote-for-witness .witness-name').on('keypress', clearWitnessInfo);
  $('#vote-for-witness .witness-name').on('focusout', (e) => getWitnessInfo(e.target.value));
  $('#vote-for-witness .vote-witness-btn').on('click', () => getTransaction((trx) => {
    $('#vote-for-witness .output').val(JSON.stringify(trx));
  }));
  $('#vote-for-witness #sign-and-broadcast .btn-broadcast').on('click', signAndBroadcast);

  if (window.location.pathname.indexOf('/vote-for-witness.html') !== -1) {
    window.scorum.api.getActiveWitnessesAsync()
      .then((res) => {
        const promises = [];
        res.forEach(witness => promises.push(window.scorum.api.getWitnessByAccountAsync(witness)));
        return Promise.all(promises);
      })
      .then((results) => {
        const sortedWitnesses = results.sort(SortByVotes);
        sortedWitnesses.reverse().forEach((witness, i) => {
          $('#vote-for-witness .witness-list').append(`<li class="list-group-item"><b>${i+1}</b> ${witness.owner} - ${numberWithCommas(witness.votes)}</li>`);
        });
      })
  }
});

const getTransaction = (callback = () => {}) => {
  const rootEl = $('#vote-for-witness');
  const yourNameEl = $('.your-name', rootEl);
  const witnessNameEl = $('.witness-name', rootEl);
  const approveEl = $('.approve', rootEl);

  const yourName = yourNameEl[0].value.trim();
  const witnessName = witnessNameEl[0].value.trim();

  const trx = {
      extensions:[],
      operations:[["account_witness_vote", { account: yourName, witness: witnessName, approve: approveEl[0].value == 1 ? false : true }]]
  }

  return window.scorum.broadcast._prepareTransaction(trx).then(preparedTrx => {
      preparedTrx.expiration = preparedTrx.expiration.toISOString().replace(/\.\d+Z/,'');

      callback(preparedTrx);

      return preparedTrx;
  });
}

const getWitnessInfo = (username) => {
  const witnessInfoEl = $('#vote-for-witness .witness-info');

  window.scorum.api.getWitnessByAccountAsync(username)
  .then((res) => {
    if (res) {
      witnessInfoEl.css('display', 'block');
      $('.info', witnessInfoEl).text(res.votes);
    }
  })
}

const signAndBroadcast = () => {
  const privateKey = $('#sign-and-broadcast .private-key')[0].value;

  getTransaction()
    .then((trx) => window.scorum.auth.signTransaction(trx, [privateKey]))
    .then(window.scorum.broadcast.send)
    .then(() => {
      $('#sign-and-broadcast .private-key')[0].value = '';
    })
};

const clearWitnessInfo = () => {
  const witnessInfoEl = $('#vote-for-witness .witness-info');
  witnessInfoEl.css('display', 'none');
  $('.info', witnessInfoEl).text('');
}

