import $ from 'jquery';
import Inputmask from "inputmask/dist/inputmask/inputmask.numeric.extensions";

export default $(function() {
  $('#transfer-scr-page #sign-and-broadcast .btn-broadcast').on('click', signAndBroadcast);
  $('#transfer-scr-page .send-from-name').on('focusout', (e) => getFromAccount(e.target.value));
  $('#transfer-scr-page #importModal .transfer-import-btn').on('click', () => {
    importCSVData();
    $('#transfer-scr-page #importModal').modal('hide');
  });
});

const importCSVData = () => {
  const textareaData = $('#transfer-scr-page #importModal #import-data')[0].value;

  const data = [];
  textareaData.split('\n').map(el => data.push(el.split(',')));
  data.forEach((line, i) => {
    $('#transfer-scr-page .imported-receivers').append(`
      <div class="row line">
        <div class="col-4">
          <input type="text" name="from-name" class="from-name form-control" placeholder="Username ..." value="${line[0].trim()}">
        </div>
        <div class="col-4">
          <div class="input-group mb-3">
            <div class="input-group-prepend">
              <span class="input-group-text">SCR</span>
            </div>
            <input type="text" class="form-control transfer-amount-scr" value="${line[1].trim()}">
          </div>
        </div>
      </div>
    `);
  });

  Inputmask({
    alias: 'decimal',
    groupSeparator: '',
    autoGroup: true,
    digits: 9,
    digitsOptional: false,
    prefix: ' ',
    showMaskOnHover: false,
    rightAlign: false,
    showMaskOnFocus: false
  }).mask($('.transfer-amount-scr'));
}

const getTransaction = (receiver, amount, callback = () => {}) => {
  const rootEl = $('#transfer-scr-page');
  const yourNameEl = $('.send-from-name', rootEl);

  const yourName = yourNameEl[0].value.trim();

  const trx = {
      extensions:[],
      operations:[["transfer", { from: yourName, to: receiver, amount: `${amount} SCR`, memo: '' }]]
  }

  return window.scorum.broadcast._prepareTransaction(trx).then(preparedTrx => {
      preparedTrx.expiration = preparedTrx.expiration.toISOString().replace(/\.\d+Z/,'');

      callback(preparedTrx);

      return preparedTrx;
  });
}

const getFromAccount = (username) => {
  const rootEl = $('#transfer-scr-page');

  window.scorum.api.getAccountsAsync([username])
  .then((res) => {
      if (res.length) {
        $('.from-values', rootEl).css('display', 'block');
        $('.from-values .my-scr .value', rootEl).text(res[0].balance);
      }
  })
}

const signAndBroadcast = () => {
  const privateKey = $('#sign-and-broadcast .private-key')[0].value;

  $('#transfer-scr-page .imported-receivers .line').each(function() {
    const username = $('.from-name', this)[0].value.trim();
    const amount = $('.transfer-amount-scr', this)[0].value.trim();
    getTransaction(username, amount)
      .then((trx) => window.scorum.auth.signTransaction(trx, [privateKey]))
      .then(window.scorum.broadcast.send)
      .then(() => {
        $('.from-name', this)[0].value = '';
        $('.transfer-amount-scr', this)[0].value = '';
        $('#sign-and-broadcast .private-key')[0].value = '';
      })
  });
};

const clearTransferInfo = () => {
  const witnessInfoEl = $('#transfer-scr-page .witness-info');
  witnessInfoEl.css('display', 'none');
  $('.info', witnessInfoEl).text('');
}
