import $ from 'jquery';
import PrivateKey from './lib/private-key';
import bip39 from 'bip39';

export default $(function() {
  $('#generate-key-page .btn-generate').on('click', generate);
});

const generate = () => {
  const memoKeyEl = $('#generate-key-page .memo-key');
  const privateKeyEl = $('#generate-key-page .private-key');
  const publicKeyEl = $('#generate-key-page .public-key');

  // generate seed
  const memo = bip39.generateMnemonic();
  // private key
  const pkInstance = PrivateKey.fromSeed(memo);
  const privKey = pkInstance.toString();

  // public key
  const pubKey = pkInstance.toPublicKey().toPublicKeyString();

  memoKeyEl.val(memo);
  privateKeyEl.val(privKey);
  publicKeyEl.val(pubKey);
}


