import $ from 'jquery';
import PrivateKey from './lib/private-key';
import bip39 from 'bip39';

export default $(function() {
  $('#restore-key-page .btn-restore').on('click', restore);
});

const restore = () => {
  const memoKeyEl = $('#restore-key-page .memo-key');
  const privateKeyEl = $('#restore-key-page .private-key');
  const publicKeyEl = $('#restore-key-page .public-key');

  const privateKey = PrivateKey.fromSeed(memoKeyEl[0].value);
  privateKeyEl.val(privateKey.toString());
  publicKeyEl.val(PrivateKey.fromWif(privateKey).toPublicKey().toPublicKeyString());
}
