import $ from 'jquery';

export default $(function() {
  $('#update-keys-page .your-name').on('keypress', clearKeys);
  $('.your-name').on('focusout', (e) => getAccount(e.target.value));
  $('#update-keys-page .update-keys-btn').on('click', () => getTransaction((trx) => {
    $('#update-keys-page .output').val(JSON.stringify(trx));
  }));
  $('#update-keys-page #sign-and-broadcast .btn-broadcast').on('click', signAndBroadcast);
});

let publicKey = '';

const getTransaction = (callback = () => {}) => {
  const rootEl = $('#update-keys-page');
  const yourNameEl = $('.your-name', rootEl);
  const newKeyEl = $('.new-key', rootEl);
  const keyTypeEl = $('.keys', rootEl);

  const yourName = yourNameEl[0].value.trim();
  const newKey = newKeyEl[0].value.trim();

  let trx = {};
  if (keyTypeEl[0].value == 'owner') {
      trx = {
          extensions: [],
          operations: [["account_update",{account: yourName, owner:{"weight_threshold":1,"account_auths":[],"key_auths":[[newKey,1]]},"memo_key":publicKey,"json_metadata":""}]]
      };
  }
  if (keyTypeEl[0].value == 'active') {
      trx = {
          extensions: [],
          operations: [["account_update",{account: yourName, active:{"weight_threshold":1,"account_auths":[],"key_auths":[[newKey,1]]},"memo_key":publicKey,"json_metadata":""}]]
      };
  }
  if (keyTypeEl[0].value == 'posting') {
      trx = {
          extensions: [],
          operations: [["account_update",{account: yourName, posting:{"weight_threshold":1,"account_auths":[],"key_auths":[[newKey,1]]},"memo_key":publicKey,"json_metadata":""}]]
      };
  }

  return window.scorum.broadcast._prepareTransaction(trx).then(preparedTrx => {
      preparedTrx.expiration = preparedTrx.expiration.toISOString().replace(/\.\d+Z/,'');

      callback(preparedTrx);

      return preparedTrx;
  });
}

const getAccount = (username) => {
  const yourKeysEl = $('#update-keys-page .your-keys');

  window.scorum.api.getAccountsAsync([username])
  .then((res) => {
      if (res.length) {
          publicKey = res[0].memo_key;

          yourKeysEl.css('display', 'block');
          $('.owner', yourKeysEl).text(res[0].owner.key_auths[0][0]);
          $('.active', yourKeysEl).text(res[0].active.key_auths[0][0]);
          $('.posting', yourKeysEl).text(res[0].posting.key_auths[0][0]);
      }
  })
}

const signAndBroadcast = () => {
  const privateKey = $('#sign-and-broadcast .private-key')[0].value;

  getTransaction()
    .then((trx) => window.scorum.auth.signTransaction(trx, [privateKey]))
    .then(window.scorum.broadcast.send)
    .then(() => {
      $('#sign-and-broadcast .private-key')[0].value = '';
    })
};

const clearKeys = () => {
  const yourKeysEl = $('#update-keys-page .your-keys');
  yourKeysEl.css('display', 'none');
  $('.owner', yourKeysEl).text('');
  $('.active', yourKeysEl).text('');
  $('.posting', yourKeysEl).text('');
}
